/** @jsx jsx */
import React from 'react';
import { jsx } from '@emotion/core'

// style variables
import { colors } from '../styles/StyleVariables'


function Card(props) {
  const hasButton = props.linkUrl && props.linkText;

  return (
    <li css={{
      position: 'relative',
      padding: '44px 16px',
      paddingBottom: hasButton ? 64 : '',
      backgroundColor: '#FFF',
      boxShadow: 'rgba(0, 0, 0, 0.14) 4px 4px 16px',
    }}>
      <h4 css={{
        marginBottom: 2,
      }}>
        {props.title}
      </h4>
      <p css={{
        color: colors.mediumGrey,
      }}>
        {props.description}
      </p>
      {props.image && (
        <img
          src={props.image}
          alt={props.imageAlt}
          {...{'data-pin-nopin': "true"}}
          css={{
            display: 'block',
            maxWidth: 88,
            height: 'auto',
          }}
          // Not sure if this is the best way to handle the 403 errors, but... works alright for now
          onError={(e) => {const img=e.target;img.onError=null;img.parentNode.removeChild(img);}}
        />
      )}
      {hasButton && (
        <a
          className="btn"
          href={props.linkUrl}
          css={{
            position: 'absolute',
            bottom: 8,
            right: 8,
          }}
        >
          {props.linkText}
        </a>
      )}
    </li>
  );
}

export default Card;
