/** @jsx jsx */
import React from 'react';
import { jsx } from '@emotion/core'

// style variables
import { colors, mq, sizes } from '../styles/StyleVariables'

// components
import BlockOfCards from './BlockOfCards'
import Explainer from './Explainer'

// images
import screensIcon from '../assets/screens.svg'


function ContentMain(props) {

  const menuOpen = props.menuCollapsible && props.menuOpen;

  return (
    <div
      css={{
        position: 'relative',
        left: props.menuCollapsible ? 'auto' : `${sizes.menuWidth}px`,
        width: props.menuCollapsible ? '100%' : `calc(100% - ${sizes.menuWidth}px)`,
        minHeight: '100vh',
        paddingTop: sizes.headerHeight + 40,
        paddingBottom: 72,
        paddingRight: 16,
        paddingLeft: 16,
        backgroundColor: colors.bgLight,
        boxShadow: 'rgba(0, 0, 0, 0.14) 4px 4px 16px',
        zIndex: 2,
        transition: 'transform 0.3s',
        transform: `translate3d(${menuOpen ? sizes.menuWidth : 0}px, 0, 0)`,

        [mq('extraSmall')]: {
          paddingRight: 32,
          paddingLeft: 32,
        },

        [mq('small')]: {
          paddingRight: 56,
          paddingLeft: 56,
        },

        [mq('large')]: {
          paddingBottom: 128,
          paddingRight: 72,
          paddingLeft: 72,
        },
      }}
    >
      <div css={{
        maxWidth: 1200,
        margin: '0 auto',
      }}>
        <h1 css={{
          marginBottom: 24,
        }}>
          Header
        </h1>
        <Explainer
          title="Explainer"
          description="This copy helps you understand what is going on on this page."
          icon={screensIcon}
        />
        <BlockOfCards />
      </div>

      {/* Overlay of ContentMain so a click can close the menu without propagating */}
      <div
        // Can extend this to a swipe motion
        onClick={(e) => {
          e.preventDefault();
          e.stopPropagation();
          props.toggleMenu();
        }}
        css={{
          display: menuOpen ? 'block' : 'none',
          position: 'absolute',
          top: 0,
          right: 0,
          bottom: 0,
          left: 0,
          zIndex: 11,
        }}
      ></div>
    </div>
  );
}

export default ContentMain;
