// Style variables


/**
 * Colors
 */
const colors = {
  darkGrey: '#323131',
  mediumGrey: '#696867',
  lightGrey: '#A8A6A5',

  crimson: '#8F0000',
  rust: '#C76D59',
  mediumPink: '#F8EEEC',
  lightPink: '#FCF8F7',

  blue: '#2E46B6',
  blueRGB: [46, 70, 182],
};

// color pseudonyms
Object.assign(colors, {
  bgLight: colors.lightPink,
  bgDark: colors.mediumPink,

  textLight: colors.mediumGrey,
  textDark: colors.darkGrey,
  textAccent: colors.rust,
  textLink: colors.rust,
  textHover: colors.crimson,

  action: colors.blue,
  actionRGB: colors.blueRGB,
});


/**
 * Fonts
 */
const fonts = {
  display: '"Rufina", serif',
  body: '"Roboto", sans-serif',
};


/**
 * Sizes
 */
const sizes = {
  headerHeight: 50,
  menuWidth: 264,
};


/**
 * Breakpoints
 */
const breakpoints = {
  extraSmall: 600,
  small: 768,
  medium: 960,
  large: 1200,
};

// breakpoints max values
Object.assign(breakpoints, {
  maxTiny: breakpoints.extraSmall - 1,
  maxExtraSmall: breakpoints.small - 1,
  maxSmall: breakpoints.medium - 1,
  maxMedium: breakpoints.large - 1,
});


/**
 * Media query builder
 *
 * Usage:
 *
 *     css={{
 *       [mq('maxSmall')]: {
 *         color: 'green'
 *       }
 *     }}
 *     css={css`
 *       ${mq('maxMedium')} {
 *         color: purple;
 *       }
 *     `}
 */
const mq = (breakpointName) => {
  // breakpointName is a string corresponding to a breakpoints object key
  if (!breakpoints[breakpointName]) {
    return '@media screen';
  }

  const minMax = breakpointName.startsWith('max') ? 'max' : 'min';
  return `@media screen and (${minMax}-width: ${breakpoints[breakpointName]}px)`
};

export { breakpoints, colors, fonts, mq, sizes }
