/** @jsx jsx */
import React from 'react';
import { jsx } from '@emotion/core';

// style variables
import { colors, sizes } from '../styles/StyleVariables';


function MenuPanel(props) {

  // We'll fake an object with the menu links
  const menuItems = new Array(7).fill().map((item, i) => {
    return {
      id: `menu-${i+1}`,
      text: `Menu ${i+1}`,
      url: '#',
    };
  });

  const menuElements = menuItems.map((item) => {
    return (
      <li
        key={item.id}
        css={{
          margin: '32px 0',
        }}
      >
        <a
          href={item.url}
          css={{
            padding: '4px 0',
          }}
        >
          {item.text}
        </a>
      </li>
    );
  });

  return (
    <nav
      aria-label="Main Navigation"
      css={{
        position: 'fixed',
        top: 0,
        left: 0,
        height: '100vh',
        width: sizes.menuWidth,
        paddingTop: sizes.headerHeight + 16,
        backgroundColor: colors.bgDark,
        transition: 'transform 0.3s',
        transform: `translate3d(${props.menuCollapsible && !props.menuOpen ? -(sizes.menuWidth/3) : 0}px, 0, 0)`,
        zIndex: 1,
      }}
    >
      <ul css={{
        listStyleType: 'none',
        maxHeight: '100%',
        padding: '0 32px',
        margin: 0,
        fontWeight: 500,
        overflowY: 'scroll',
      }}>
        {menuElements}
      </ul>
    </nav>
  );
}

export default MenuPanel;
