/** @jsx jsx */
import React, { Component } from 'react';
import { css, jsx } from '@emotion/core'

// style variables
import { colors } from '../styles/StyleVariables'

// components
import Icon from './Icon'


class AccountLinks extends Component {
  constructor(props) {
    super(props);

    this.state = {
      accountMenuOpen: false,
    };

    // We'll fake an object with the account links
    this.accountLinks = new Array(4).fill().map((item, i) => {
      return {
        id: `user-link-${i+1}`,
        text: `User Link ${i+1}`,
        url: '#',
      };
    });

    this.handleClick = this.handleClick.bind(this);
  }

  // Toggle menu open/closed
  handleClick(event) {
    event.preventDefault();

    this.setState((prevState) => ({
      accountMenuOpen: !prevState.accountMenuOpen
    }));
  }
  
  render() {
    const sideMenuOpen = this.props.menuOpen;
    const accountMenuOpen = this.state.accountMenuOpen;

    // Hide when side menu is open on small screens
    const hiddenCSS = this.props.menuCollapsible && sideMenuOpen ? {
      opacity: 0,
      pointerEvents: 'none',
      cursor: 'not-allowed',
    } : {};

    return (
      <div style={{
        position: 'relative',
        marginLeft: 'auto',
        zIndex: 10,
        transition: 'opacity 0.3s',
        ...hiddenCSS,
      }}>
        <span
          onClick={this.handleClick}
          css={{
            fontSize: '0.9375em',
            cursor: 'pointer',
            '&:hover': {
              '&, span': {
                color: colors.textDark
              },
              i: {
                color: colors.textLight
              },
            }
          }}
        >
          <span css={{
            color: colors.textLight
          }}>
            {this.props.loggedin ? this.props.username : 'Log In'}
          </span>
          <Icon
            name="chevron-down"
            css={{
              marginLeft: 12,
              transition: 'transform 0.2s',
              transform: `scaleY(${accountMenuOpen ? -1 : 1})`,
            }}
          />
        </span>
        <nav
          aria-label="User Account Links"
          css={css`
            display: ${accountMenuOpen ? 'block' : 'none'};
            position: absolute;
            top: calc(100% + 16px);
            right: 0;
            padding: 16px 20px;
            background: #FFF;
            box-shadow: rgba(0, 0, 0, 0.14) 4px 4px 16px;
            min-width: 200px;

            &:after {
              content: '';
              display: block;
              position: absolute;
              bottom: 100%;
              right: 12px;
              border-style: solid;
              border-color: transparent transparent #FFF transparent;
              border-width: 0px 12px 8px 12px;
            }
          `}
        >
          <ul css={{
            listStyleType: 'none',
            padding: 0,
            margin: 0,
          }}>
            {this.accountLinks.map((link) => (
              <li
                key={link.id}
                css={{
                  padding: '4px 0',
                  margin: '4px 0',
                  whiteSpace: 'nowrap',
                }}
              >
                <a href={link.url}>{link.text}</a>
              </li>
            ))}
          </ul>
        </nav>
      </div>
    );
  }
}

export default AccountLinks;
