/** @jsx jsx */
import { Component } from 'react';
import { jsx } from '@emotion/core'

// style variables
import { breakpoints, mq } from '../styles/StyleVariables'

// components
import ActionButton from './ActionButton'
import Card from './Card'


class BlockOfCards extends Component {
  constructor(props) {
    super(props);

    this.state = {
      searchTerm: 'coach',
      resultsPage: 1,
      cardData: [],
      loading: false,
      allResultsLoaded: false,
    };

    this.fetchMovies = this.fetchMovies.bind(this);
    this.addMoreResults = this.addMoreResults.bind(this);
  }

  componentDidMount() {
    this.fetchMovies();
  }

  addMoreResults() {
    if (this.state.allResultsLoaded) {
      return;
    }
    this.setState((prevState) => ({
      resultsPage: prevState.resultsPage + 1
    }), () => {
      this.fetchMovies(this.state.resultsPage);
    });
  }

  fetchMovies(page) {
    if (this.state.searchTerm.trim() === '') {
      return;
    }

    this.setState({
      loading: true
    });

    const apikey = 'a4d98298';
    const url = `http://www.omdbapi.com/?s=${this.state.searchTerm}&apikey=${apikey}${page ? '&page=' + page : ''}`;

    fetch(url)
      // This method handles more errors than just network or permissions (e.g., 404)
      // https://developer.mozilla.org/en-US/docs/Web/API/Fetch_API/Using_Fetch#Checking_that_the_fetch_was_successful
      .then(response => {
        if (!response.ok) {
          throw Error(response.statusText);
        }
        return response;
      })
      .then(response => response.json())
      .then(data => {
        if (data.Response === "True") {
          this.setState((prevState) => {
            // Assuming data.Search will exist if data.Response is "True"
            const newCardData = prevState.cardData.concat(data.Search);
            const totalResults = data.totalResults && parseInt(data.totalResults, 10);
            const allResultsLoaded = !isNaN(totalResults) ? totalResults <= newCardData.length : false;
            return {
              cardData: newCardData,
              allResultsLoaded: allResultsLoaded,
              loading: false
            };
          });
        } else {
          this.setState({
            allResultsLoaded: true,
            loading: false
          });
        }
      })
      .catch((error) => {
        // Handle fetch errors however you want here
        // (e.g., set an error state that can show a message to the user)
        this.setState({
          loading: false
        });
      });
  }

  render() {

    const loading = this.state.loading;
    const hasResults = !!this.state.cardData.length;
    const allResultsLoaded = this.state.allResultsLoaded;
    const showAddMoreButton = !loading && hasResults && !allResultsLoaded;

    // Could get more creative with these
    const message = (() => {
      if (loading) {
        return 'Loading results...';
      } else if (!hasResults) {
        return 'Sorry, no results found.';
      } else if (allResultsLoaded) {
        return 'All results loaded.';
      }
      // Non-breaking space, as a placeholder for height
      return '\u00A0';
    })();

    const hiddenButtonCSS = showAddMoreButton ? {} : {
      opacity: 0,
      pointerEvents: 'none',
      cursor: 'not-allowed',
    };

    const cards = this.state.cardData.map((movie) => {
      return (
        <Card
          key={movie.imdbID}
          title={movie.Title}
          description={movie.Year}
          image={movie.Poster === 'N/A' ? null : movie.Poster}
          imageAlt={`Movie poster for ${movie.Title}`}
          linkUrl={`https://www.imdb.com/title/${movie.imdbID}`}
          linkText="View on IMDB"
        />
      );
    });

    return (
      <section>
        <h3 css={{
          marginBottom: 12,
        }}>
          {`Movie results for "${this.state.searchTerm}"`}
        </h3>

        {hasResults && (
          <ul css={{
            display: 'grid',
            gridTemplateColumns: '1fr',
            gridGap: '8px 0',
            listStyleType: 'none',
            padding: 0,
            margin: 0,

            [mq('small')]: {
              gridTemplateColumns: '1fr 1fr',
              gridGap: '24px 48px',
            },

            [`@media screen and (min-width: ${breakpoints.medium}px) and (max-width: ${breakpoints.maxMedium}px)`]: {
              gridGap: '24px 32px',
            },
          }}>
            {cards}
          </ul>
        )}

        <p css={{
          margin: '16px 0',
          fontSize: '1.125rem',
        }}>
          {message}
        </p>

        <ActionButton
          handleClick={this.addMoreResults}
          text="Add content"
          iconName="plus"
          extraCSS={{
            [mq('medium')]: {
              marginLeft: 24,
            },
            ...hiddenButtonCSS,
          }}
        />
      </section>
    );
  }
}

export default BlockOfCards;
