/** @jsx jsx */
import { Component } from 'react';
import { jsx } from '@emotion/core'

// style variables
import { colors } from '../styles/StyleVariables'

// components
import Icon from './Icon'


class ActionButton extends Component {
  constructor(props) {
    super(props);

    this.state = {
      hovered: false
    };

    this.handleHover = this.handleHover.bind(this);
  }

  handleHover(hovered) {
    this.setState({
      hovered: hovered
    });
  }

  render() {
    const iconHoverStyle = this.state.hovered ? {
      backgroundColor: `rgba(${[...colors.actionRGB, 0.7].join(',')})`,
    } : {};

    return (
      <div
        onClick={this.props.handleClick}
        css={{
          display: 'inline-block',
          fontSize: '1.5rem',
          fontWeight: 500,
          cursor: 'pointer',

          ...this.props.extraCSS,
        }}
        onMouseEnter={() => {this.handleHover(true);}}
        onMouseLeave={() => {this.handleHover(false);}}
      >
        <span css={{
          verticalAlign: 'middle',
          color: this.state.hovered ? colors.mediumGrey : '',
        }}>
          {this.props.text}
        </span>
        <Icon
          name={this.props.iconName}
          css={{
            display: 'inline-flex',
            flexDirection: 'row',
            alignItems: 'center',
            justifyContent: 'center',
            width: 40,
            height: 40,
            marginLeft: 32,
            borderRadius: '50%',
            backgroundColor: colors.action,
            color: colors.bgLight,
            fontSize: '1rem',
            textAlign: 'center',
            verticalAlign: 'middle',

            ...iconHoverStyle,
          }}
        />
      </div>
    );
  }
}

export default ActionButton
